
/* button */

button {
	display:inline-block;
	cursor:pointer;
	text-decoration:none;
}
button:active {
	position:relative;
	top:1px;
} 

.button {
    background-color: #fff;
    border-color: #dbdbdb;
    border-width: 1px;
    color: #363636;
    cursor: pointer;
    justify-content: center;
    padding-bottom: calc(.375em - 1px);
    padding-left: .75em;
    padding-right: .75em;
    padding-top: calc(.375em - 1px);
    text-align: center;
    white-space: nowrap;
}

.btn {
	position: relative;
	color: white;
	max-width: 256px;
	width: 26vw;
	height: 64px;
	line-height: 64px;
	transition: all 0.3s;
	
  }
  
  .btn::before, .btn::after {
	content: '';
	position: absolute;
	transition: all 0.3s;
	bottom: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 1;
  }

  .btn--small {
	width: 5rem;
}



 .btn-center-press {
	border: 2px solid rgba(255, 255, 255, 0.5);
	/* border: 2px solid #FFF; */
 }

/* BTN SPINNER */
.btn-spinner::before, .btn-spinner::after {
	content: '';
	position: absolute;
	width: 100%;
	height: 100%;
	bottom: 0;
	left: 0;
	z-index: 1;
	transition: all 0.3s;
	border: 1px solid rgba(255, 255, 255, 0.3);
  }
  
  .btn-spinner:hover::after {
	animation-name: rotatecw;
	animation-duration: 2s;
  }
  .btn-spinner:hover::before {
	animation-name: rotateccw; 
	animation-duration: 3s;
  }
  .btn-spinner:hover::after, .btn-spinner:hover::before {
	left: 7.5vw;
	width: 4rem;
	animation-iteration-count: infinite;
	animation-timing-function: linear;
  }
  
 


/* BTN center-press */
.btn-center-press::before {
	transition: transform 0.15s, 0.15s border-radius 0.15s;
	opacity: 0;
	background: rgba(255, 255, 255, 0.1);
	transform: scale(0.1, 0.1);
	border-radius: 50%;
  }
  
  .btn-center-press:hover::before {
	opacity: 1;
	transform: scale(1, 1);
	border-radius: 0;
  }
  
  .btn-center-press::after {
	transition: all 0.3s;

}
  
  .btn-center-press:hover::after {
	opacity: 0;
  }