@keyframes slide-in {
    0%{
        opacity: 0;
        transform: translateX(-90%);
    }

    100%{
        opacity: 1;
        transform: translateX(0px);
    }
}

@keyframes slide-up {
    0% {
        opacity: 0;
        transform: translateY(32px);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes rotatecw {
    from {transform: rotate(0deg);}
    to {transform: rotate(360deg);}
}

@keyframes rotateccw {
    from {transform: rotate(0deg);}
    to {transform: rotate(-360deg);}
}
